.day {
    padding-bottom: 50px;
}

.title {
    font-weight: 400;
    margin-top: 30px;
}

.location-name {
    font-weight: 300;
    font-size: 15px;
}

.temperature {
    font-size: 70px;
    font-weight: 200;
}

.current-weather {
    font-size: 15px;
}

.high-low-temp {
    font-size: 15px;
    margin-top: 15px;
}

.next-item {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 50px;
}

.listitem {
    margin: none;
    padding: none;
    list-style-type: none;
    border-radius: 10px;
    max-height: 620px;
    overflow: auto;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.05);
}

ul {
    padding: none;
    margin: none;
}

.listgroupitem {
    opacity: 0.5;
    font-size: 20px;
}

.listgroupitem1 {
    margin-right: 100px;
    padding: none;
}

.listdiv {
    width: 250px;
    display: flex;
    flex-direction: row;
    border-top: solid rgb(86, 86, 86);
    justify-content: space-evenly;
    align-items: center;
    margin-top: 10px;
    padding-top: 10px;
}

.listdiv p {
    margin: 5px;
}

.right-grid1 {
    width: 70vw;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.sunrise {
    width: 270px;
    height: 160px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.05);
}

.sunrise h5 {
    opacity: 0.5;
    text-align: left;
    margin-left: 20px;
    margin-top: 15px;
}

.sunrise-val {
    text-align: left;
    margin-left: 20px;
    margin-top: 15px;
    font-size: 25px;
}

.sunset-val {
    text-align: left;
    margin-left: 20px;
    margin-top: 15px;
    opacity: 0.5;
}

.small-p {
    font-size: 10px;
}

.right-grid2 {
    margin-top: 50px;
    width: 70vw;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.sunrise-long {
    width: 590px;
    height: 160px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.05);
}

.sunrise-long h5 {
    opacity: 0.5;
    text-align: left;
    margin-left: 20px;
    margin-top: 15px;
}

.right-grid3 {
    margin-top: 50px;
    width: 70vw;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.sunrise-long1 {
    width: 590px;
    height: 200px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.05);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.sunrise-long1 h5 {
    opacity: 0.5;
    text-align: left;
    margin-left: 20px;
    margin-top: 15px;
}

.sunrise-long1 img {
    width: 100px;
    margin-right: 50px;
}

.sunrise-thought {
    width: 270px;
    height: 200px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.05);
}

.sunrise-thought h5 {
    opacity: 0.5;
    text-align: left;
    margin-left: 20px;
    margin-top: 15px;
}

.night-theme {
    text-align: center;
    background-color: black;
    background-image: url("./backgrounds/night-sky.jpg");
    color: white;
}

.day-theme {
    text-align: center;
    background-color: white;
    background-image: url("./backgrounds/morning-sky.jpg");
    color: black;
}

.vincent {
    width: 100%;
    position: fixed;
    top: 0;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    border-top: solid 4px rosybrown;
    /* color: black; */
    opacity: 100;
}

.vincent p {
    padding: 8px;
    font-family: "Exo 2", sans-serif;
    font-weight: 600;
    font-size: 15px;
    background-color: rosybrown;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    padding-bottom: 10px;
}

.search span {
    cursor: pointer;
}